import React from "react";
import styled from "styled-components";
import { Subtotal, CheckoutProduct } from "../components/index";
import { useStateValue } from "../providers/StateProvider";
// styled componets
const CheckoutContainer = styled.div`
  display: flex;
  padding: 20px;
  background: #fff;
  height: max-content;
`;
const CheckoutLeft = styled.div``;
const CheckoutRigth = styled.div``;
const BannerAdd = styled.img`
  width: 100%;
  margin-bottom: 10px;
`;
const CheckoutTitle = styled.h2`
  margin-right: 10px;
  padding: 10px;
  border-bottom: 1px solid lightgray;
`;

const Checkout = () => {
  const [{ basket }, dispatch] = useStateValue();
  return (
    <CheckoutContainer>
      <CheckoutLeft>
        <BannerAdd src="https://compradete.com/wp-content/uploads/2018/12/banner-ofertas-amazon.jpg" />
        <div>
          <CheckoutTitle>your Shopping Bazket</CheckoutTitle>
          {basket.map((item) => (
            <CheckoutProduct
              id={item.id}
              title={item.title}
              image={item.image}
              price={item.price}
              rating={item.rating}
            />
          ))}
        </div>
      </CheckoutLeft>
      <CheckoutRigth>
        <Subtotal />
      </CheckoutRigth>
    </CheckoutContainer>
  );
};
export default Checkout;
