import React from "react";
//dependencies
import styled from "styled-components";
import { Link } from "react-router-dom";
//material ui
import SearchIcon from "@material-ui/icons/Search";
import ShoppingBasketIcon from "@material-ui/icons/ShoppingBasket";
//redux
import { useStateValue } from "../providers/StateProvider";
//firebase
import { auth } from "../firebase";
//styled compoenents
const HeaderContainer = styled.div`
  height: 60px;
  display: flex;
  align-items: center;
  background: #131921;
  position: sticky;
  top: 0;
  z-index: 100;
  padding: 0 10px;
`;
const LogoImg = styled.img`
  width: 100px;
  object-fit: contain;
  margin-right: 10px;
  margin-top: 18px;
`;
const Search = styled.div`
  display: flex;
  flex: 1;
  align-items: center;
  border-radius: 24px;
`;
const SearchIput = styled.input`
  padding: 10px;
  height: 12px;
  border: none;
  width: 100%;
`;
const SearchIcon__ = styled(SearchIcon)`
  padding: 5px;
  height: 22px;
  background-color: #cd9042;
`;
const Nav = styled.div`
  display: flex;
  justify-content: space-evenly;
`;
const NavItem = styled.div`
  display: flex;
  flex-direction: column;
  margin: 0 10px;
  color: #fff;
  & .basket {
    display: flex;
    align-items: center;
    color: #fff;
  }
`;
const NavItem__line1 = styled.span`
  font-size: 10px;
`;
const NavItem__line2 = styled.span`
  font-size: 13px;
  font-weight: 800;
  & .basketCount {
    margin: 0 10px;
  }
`;
const Header = () => {
  const [{ basket, user }, dispatch] = useStateValue();
  const handleAuthentication = () => {
    if (user) {
      auth.signOut();
    }
  };
  return (
    <HeaderContainer>
      <Link to="/">
        <LogoImg src="http://pngimg.com/uploads/amazon/amazon_PNG11.png" />
      </Link>

      <Search>
        <SearchIput />
        <SearchIcon__ />
      </Search>
      <Nav>
        <Link to={!user && "/login"}>
          <NavItem onClick={handleAuthentication}>
            <NavItem__line1>Hello</NavItem__line1>

            <NavItem__line2>{user ? "Sign Out" : "Sign In"}</NavItem__line2>
          </NavItem>
        </Link>
        <NavItem>
          <NavItem__line1>Return</NavItem__line1>

          <NavItem__line2>Orders</NavItem__line2>
        </NavItem>

        <NavItem>
          <NavItem__line1>Your</NavItem__line1>

          <NavItem__line2>Prime</NavItem__line2>
        </NavItem>
        <Link to="/checkout">
          <NavItem className="basket">
            <ShoppingBasketIcon />
            <span className="basketCount">{basket?.length}</span>
          </NavItem>
        </Link>
      </Nav>
    </HeaderContainer>
  );
};
export default Header;
