import React from "react";
//dependencies
import styled from "styled-components";
import { useStateValue } from "../providers/StateProvider";
//styled components
const ProductCard = styled.div`
  display: flex;
  flex-direction: column;
  background: #fff;
  z-index: 1;
  align-items: center;
  justify-content: flex-end;
  margin: 10px;
  padding: 20px;
  width: 100%;
  min-width: 100px;
  max-height: 400px;
`;
const ProductInfo = styled.div`
  height: 100px;
  margin-bottom: 15px;
  text-align: left;
`;
const Price = styled.p`
  margin-top: 5px;
`;
const Rating = styled.div`
  display: felx;
`;
const ProductImg = styled.img`
  max-height: 200px;
  width: 100%;
  object-fit: contain;
  margin-bottom: 15px;
`;
const Button = styled.button`
  background: #f0c14b;
  border: 1px solid;
  margin-top: 10px;
  border-color: #a88734 #9c7e31 #846a29;
  color: #111;
`;
const Product = ({ id, title, image, price, rating }) => {
  const [{ basket }, dispatch] = useStateValue();

  const addToBasket = () => {
    dispatch({
      type: "ADD_TO_BASKET",
      item: {
        id: id,
        title: title,
        image: image,
        price: price,
        rating: rating
      }
    });
  };

  return (
    <ProductCard>
      <ProductInfo>
        <p>{title}</p>
        <Price>
          <small>$</small>
          <strong>{price}</strong>
        </Price>
        <Rating>
          {Array(rating)
            .fill()
            .map((_, i) => (
              <span>⭐</span>
            ))}
        </Rating>
      </ProductInfo>
      <ProductImg src={image} />
      <Button onClick={addToBasket}>Add to Basket</Button>
    </ProductCard>
  );
};

export default Product;
