import React, { useState } from "react";
//dependencies
import styled from "styled-components";
import { Link, useHistory } from "react-router-dom";
import { auth } from "../firebase";
//styled components
const LoginContainer = styled.div`
  background: #fff;
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
`;
const LoginCard = styled.div`
  display: flex;
  flex-direction: column;
  width: 300px;
  height: fit-content;
  border-radius: 5px;
  border: 1px solid lightgray;
  padding: 20px;
`;
const Title = styled.h1`
  font-weight: 500;
  margin-bottom: 20px;
`;
const Formulario = styled.form``;
const Label = styled.h5`
  margin-bottom: 5px;
`;
const Input = styled.input`
  width: 98%;
  margin-bottom: 10px;
  height: 30px;
  background: #fff;
`;
const BtnLogin = styled.button`
  background: #f0c14b;
  border-radius: 2px;
  width: 100%;
  height: 30px;
  border: 1px solid;
  margin-top: 10px;
  border-color: #a88734 #9c7e31 #846a29;
  color: #111;
`;
const BtnRegister = styled.button`
  border-radius: 2px;
  width: 100%;
  height: 30px;
  border: 1px solid;
  margin-top: 10px;
  border-color: darkgray;
`;
const LoginImg = styled.img`
  margin: 20px auto;
  object-fit: contain;
  width: 100px;
`;
const Login = () => {
  const history = useHistory();
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");

  const signIn = (e) => {
    e.preventDefault();
     auth
     .signInWithEmailAndPassword(email, password)
     .then(auth =>{
       history.push('/')
     })
     .catch(error => alert(error.message))
     
  };

  const register = (e) => {
    e.preventDefault();
    auth
      .createUserWithEmailAndPassword(email, password)
      .then((auth) => {
        console.log(auth);
        if(auth){
          history.push('/')
        }
      })
      .catch((error) => alert(error.message));
  };
  return (
    <LoginContainer>
      <Link to="/">
        <LoginImg src="https://upload.wikimedia.org/wikipedia/commons/a/a9/Amazon_logo.svg" />
      </Link>
      <LoginCard>
        <Title>Sing In </Title>
        <Formulario>
          <Label>e-mail </Label>
          <Input
            type="text"
            value={email}
            onChange={(e) => setEmail(e.target.value)}
          />
          <Label>password </Label>
          <Input
            type="password"
            value={password}
            onChange={(e) => setPassword(e.target.value)}
          />
          <BtnLogin type="submit" onClick={signIn}>
            Sign In
          </BtnLogin>
        </Formulario>
        <p>Terminos y condiciones</p>
        <BtnRegister onClick={register}>Create Account</BtnRegister>
      </LoginCard>
    </LoginContainer>
  );
};
export default Login;
